body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center;display:flex;flex-direction:column}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.wrapper{margin:0 auto;box-sizing:content-box;min-height:80vh;min-width:80vw;background-color:#5f7e8d}

.buttonInner{background-color:#1976D2;min-height:35px;min-width:100px;border:1px solid #1976D2;border-radius:5px;color:mediumspringgreen;outline:none;text-transform:uppercase}.buttonInner:active{opacity:80%}.buttonInner:hover{background-color:#115293}.buttonOuter{box-sizing:border-box;letter-spacing:1px;margin-bottom:15px;padding:7px 20px;text-align:center;text-transform:uppercase;text-decoration:none;outline:none;max-width:170px;border:none;color:white;font-weight:bold;position:relative;z-index:0;background-color:#9049ff}.buttonOuter::before{height:8px;width:50%;margin:0 auto;content:"";position:absolute;left:0;right:0;bottom:0;height:0;z-index:-1;background:#ffd800;box-sizing:border-box;transition-duration:.4s}.buttonOuter:hover::before{height:30px;width:100%}.buttonOuter:active::before{background-color:red;box-shadow:0 0 10px rgba(0,0,0,0.5);height:30px;width:80%}

.inputCheckBox{background-color:#1976D2;border:1px solid #1976D2;outline:none}.inputCheckBox:active{opacity:80%}.inputCheckBox:hover{background-color:#115293}

.styled-checkbox{position:absolute;opacity:0}.styled-checkbox+label{position:relative;cursor:pointer;padding:0}.styled-checkbox+label:before{content:'';margin-right:10px;display:inline-block;vertical-align:text-top;width:20px;height:20px;background:#1976D2;border-radius:3px}.styled-checkbox:hover+label:before{background:#115293}.styled-checkbox:focus+label:before{box-shadow:0 0 0 3px rgba(0,0,0,0.12)}.styled-checkbox:checked+label:before{background:#115293}.styled-checkbox:disabled+label{color:#b8b8b8;cursor:auto}.styled-checkbox:disabled+label:before{box-shadow:none;background:#ddd}.styled-checkbox:checked+label:after{content:'';position:absolute;left:5px;top:9px;background:white;width:2px;height:2px;box-shadow:2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;transform:rotate(45deg)}

.navItem{padding:5px 10px}.navItem a{color:white;text-decoration:none}

